import {
  GET_BLOG_POSTS_SUCCESS,
  GET_BLOG_POSTS_BEGIN,
  GET_BLOG_POSTS_ERROR
} from '../constants';

const initial = {
  posts: [],
  categories: [],
  filters: {
    post: {}
  }
};

export default function blog(state=initial, action) {
  switch (action.type) {
    case GET_BLOG_POSTS_BEGIN:
      return {
        isFetching: true
      };
    case GET_BLOG_POSTS_SUCCESS:
      return {
        posts: state.posts,
        categories: state.categories,
        isFetching: false
      };
    case GET_BLOG_POSTS_ERROR:
      return {
        isFetching: false
      };
    default:
      return state;
  }
}


